* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.color-button {
  border-radius: 0;
  padding: 15px;
}

img {
  max-width: 100%;
  display: block;
}

.container {
  flex-grow: 1;
  align-items: baseline;
  display: flex;
}

.form {
  align-items: stretch;
  margin-left: 15px;
  display: flex;
}

.form label {
  margin-right: 10px;
}

.form input {
  height: 100%;
  padding: 5px;
}

.promises-list {
  margin-left: auto;
  padding: 0;
  list-style: none;
}

.form button {
  border-radius: 0;
  padding: 10px;
}

.promises-item {
  color: #fff;
  width: 100%;
  text-align: center;
  border-radius: 8px;
  padding: 10px;
}

.promises-item:not(:last-child) {
  margin-bottom: 15px;
}

.fulfilled-text {
  background-color: rgba(0, 128, 0, .6);
}

.rejected-text {
  background-color: rgba(255, 0, 0, .7);
}

